import { defineStore } from "pinia";
import apiClient from "@/plugins/apiClient";

export const useShopsStore = defineStore("shopsStore", {
  state: () => ({
    shops: null,
    loading: false,
    fetchedAll: false,
  }),

  getters: {
    getById: (state) => (id) => {
      return (state.shops || []).find((shop) => shop.id == id);
    },
  },

  actions: {
    async fetchShops(force = false) {
      if (this.fetchedAll && !force) return;

      try {
        this.loading = true;
        const response = await apiClient.get("/stores");
        this.shops = (response.data).map(mapStoreData);
        this.fetchedAll = true;
      } catch (err) {
        console.error("Failed to load shops", err);
      } finally {
        this.loading = false;
      }
    },

    async fetchSingleShop(id) {
      const existingShop = this.getById(id);
      if (existingShop) return false;

      try {
        this.loading = true;
        const response = await apiClient.get(`/stores/${id}`);
        const newShop = mapStoreData(response.data);
        this.shops = this.shops ? [...this.shops, newShop] : [newShop];
        return newShop;
      } catch (err) {
        console.error("Failed to load shop", err);
      } finally {
        this.loading = false;
      }
    },
  },
});

function mapStoreData(item) {
  return {
    id: item.id,
    name: item.name,
    description:
      !item.description || item.description === "-"
        ? "No description"
        : item.description,
    image: item.imageUrl,
    vendor: {
      id: item.vendorId,
      name: item.vendorName,
      email: item.vendorEmail,
      phone: item.vendorPhoneNumber,
    },
    address: {
      line1: item.storeAddress.addressLine1,
      line2: item.storeAddress.addressLine2,
      city: item.storeAddress.city,
      postalCode: item.storeAddress.postalCode,
      country: item.storeAddress.country,
      location: {
        latitude: item.storeAddress.location?.latitude,
        longitude: item.storeAddress.location?.longitude,
      },
    },
  };
}