import { defineStore } from "pinia";
import apiClient from "@/plugins/apiClient";

export const useProductsStore = defineStore("productsStore", {
  state: () => ({
    productOffers: null,
    loading: false,
  }),
  getters: {
    getById: (state) => (id) => {
      return (state.productOffers || []).find((product) => product.id === id);
    },
  },
  actions: {
    async fetchProductOffers() {
      if (this.productOffers || this.loading) return;

      try {
        this.loading = true;
        const response = await apiClient.get('/product-offers')
        this.productOffers = mapProductOfferData(response.data);
      } catch (err) {
        console.error("Failed to load shops", err);
      } finally {
        this.loading = false;
      }
    },

    async fetchShopProducts(shopId) {
      try {
        this.loading = true;
        const response = await apiClient.get(`/product-offers?store_id=${shopId}`);
        return mapProductOfferData(response.data);
      } catch (err) {
        console.error("Failed to load shop products", err);
      } finally {
        this.loading = false;
      }
    }
  },
});

function mapProductOfferData(dataArray) {
  return dataArray.map(item => ({
    id: item.productOfferId,
    image: item.imageUrl,
    price: item.price,
    title: item.productName,
    description: item.productOfferDescription,
    productId: item.productId,
    store: {
      id: item.storeId,
      name: item.storeName
    },
    vendor: {
      id: item.vendorId,
      name: item.vendorName,
    },
    unit: {
      name: item.unitName,
      description: item.unitDescription,
    }
  }));
}

